*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-font: "Roboto", sans-serif;
  --main-light: #fff;
  --main-yellow: #ffe550;
  --accent-cl: #bbf330;
  --main-green: #7eb101;
  --hover-btn: #b5ff00;
  --hover-btn-border: #597d00;
  --main-font-cl: #2c2c2c;
  --secondary-font-cl: #000;
  --placeholder-font-cl: #505050;
  --form-font-cl: #c4c4c4;
  --form-red: #ff705d;
  --light-bg-cl: #fefefe;
  --backdrop-bg-cl: rgba(94, 99, 99, .7);
  --indent: 30px;
  --items: 3;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
  --animation-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.footer__list, .customers__list, .favorites__list, .offerings__list, .nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  color: currentColor;
  text-decoration: none;
}

.offerings__btn, .hero__btn, .header__btn, .order-form__btn, .subscription__btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 30px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.54;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--primary-font);
  color: var(--main-font-cl);
  background-color: var(--main-light);
  font-size: 14px;
  position: relative;
}

.no-scroll {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  display: block;
}

.icon {
  fill: currentColor;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.main-title {
  color: var(--main-font-cl);
  font-size: 28px;
  font-weight: 700;
  line-height: 1.18;
}

@media screen and (min-width: 768px) {
  .main-title {
    font-size: 42px;
    font-weight: 700;
    line-height: 1.19;
  }
}

@media screen and (min-width: 1280px) {
  .main-title {
    font-size: 48px;
    font-weight: 700;
    line-height: 1.21;
  }
}

.section-title {
  font-size: 28px;
  font-weight: 700;
  line-height: 1.18;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 42px;
    font-weight: 700;
    line-height: 1.17;
  }
}

.card-set {
  gap: var(--indent);
  flex-wrap: wrap;
  display: flex;
}

.card-set__item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
}

@media screen and (min-width: 768px) {
  .nav {
    gap: 28px;
    display: flex;
  }
}

.nav__link {
  color: var(--secondary-font-cl);
  transition: color var(--animation);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  position: relative;
}

.nav__link:after {
  content: "";
  opacity: 0;
  width: 100%;
  height: 1px;
  background-color: var(--accent-cl);
  transition: opacity var(--animation);
  border-radius: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover, .nav__link:focus {
  color: var(--accent-cl);
}

.nav__link:hover:after, .nav__link:focus:after {
  opacity: 1;
}

.slider {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 767px) {
  .slider {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .slider {
    width: 336px;
    height: 320px;
    top: 89px;
    right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .slider {
    width: 608px;
    height: 580px;
    top: 14px;
    right: 0;
  }
}

@media screen and (min-width: 768px) {
  .swiper {
    width: 320px;
    height: 320px;
    border: 14px solid var(--main-yellow);
    border-radius: 50%;
  }
}

@media screen and (min-width: 1280px) {
  .swiper {
    width: 580px;
    height: 580px;
    border-width: 26px;
  }
}

.swiper-slide {
  justify-content: center;
  align-items: center;
  display: flex;
}

.swiper-button-prev, .swiper-button-next {
  z-index: 100;
  width: 34px;
  height: 28px;
  color: rgba(0, 0, 0, 0);
  background: #fefefe;
  border-radius: 8px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

@media screen and (min-width: 1280px) {
  .swiper-button-prev, .swiper-button-next {
    width: 48px;
    height: 40px;
  }
}

.swiper-button-prev:after, .swiper-button-next:after {
  display: none;
}

.swiper-button-prev:active, .swiper-button-next:active {
  box-shadow: 0 4px 4px rgba(0, 0, 0, .16);
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.swiper-icon {
  width: 12px;
  height: 20px;
}

@media screen and (min-width: 1280px) {
  .swiper-icon {
    width: 16px;
    height: 29px;
  }
}

.swiper-button-prev:hover .swiper-icon, .swiper-button-prev:focus .swiper-icon, .swiper-button-next:focus .swiper-icon, .swiper-button-next:hover .swiper-icon {
  fill: var(--main-green);
}

.swiper-button-next:active .swiper-icon, .swiper-button-prev:active .swiper-icon {
  fill: var(--main-font-cl);
}

.read-more {
  max-width: 200px;
  color: var(--secondary-font-cl);
  margin-left: 24px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.43;
}

@media screen and (min-width: 1280px) {
  .read-more {
    display: none;
  }
}

.more {
  align-items: center;
  display: inline-flex;
}

.more:after {
  content: url("readmore.834b96d5.svg");
  width: 14px;
  height: 14px;
  display: inline-block;
}

.extra-text {
  margin: 20px 0;
  display: none;
}

#read-more:checked ~ .extra-text {
  display: block;
}

.less {
  align-items: center;
  display: none;
}

#read-more:checked ~ .read-more .more {
  display: none;
}

#read-more:checked ~ .read-more .less {
  display: inline-flex;
}

.less:after {
  content: url("readmore.834b96d5.svg");
  width: 14px;
  height: 14px;
  display: inline-block;
  transform: rotate(-180deg);
}

@media screen and (min-width: 768px) {
  .subscription-form {
    gap: 32px;
    display: flex;
  }
}

.subscription-form__group {
  display: block;
  position: relative;
}

@media screen and (max-width: 767px) {
  .subscription-form__group {
    margin-bottom: 20px;
  }
}

.subscription-form__input {
  width: 100%;
  border: 1px solid var(--main-yellow);
  color: var(--main-light-cl);
  transition: border-color var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 12px 11px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.subscription-form__input:focus, .subscription-form__input:not(:placeholder-shown) {
  border-color: var(--main-yellow);
}

.subscription-form__input::-ms-input-placeholder {
  color: var(--placeholder-font-cl);
  transition: color var(--animation);
}

.subscription-form__input::placeholder {
  color: var(--placeholder-font-cl);
  transition: color var(--animation);
}

.subscription-form__input:focus::-ms-input-placeholder {
  color: var(--contacts-form-cl);
}

.subscription-form__input:focus::placeholder {
  color: var(--contacts-form-cl);
}

.subscription-form__input:required:valid:not(:placeholder-shown) {
  border-color: var(--accent-cl);
}

.subscription-form__input:required:invalid:not(:placeholder-shown) {
  border-color: var(--form-red);
}

.subscription-form__label {
  color: var(--placeholder-font-cl);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
  position: absolute;
  top: 10px;
  left: 0;
}

.subscription-form__notification {
  width: 100%;
  opacity: 0;
  transition: opacity var(--animation);
  position: absolute;
  bottom: -15px;
  left: 0;
}

.subscription-form__input:required:invalid:not(:placeholder-shown) ~ .subscription-form__notification {
  opacity: 1;
}

.subscription-form__desc {
  text-align: left;
  color: var(--form-red);
  font-size: 11px;
  font-weight: 400;
  line-height: 1.36;
}

.subscription__btn {
  padding: 11px 22px;
  display: block;
}

@media screen and (max-width: 767px) {
  .subscription__btn {
    margin-left: auto;
  }
}

.card {
  background-color: var(--main-light);
  max-width: 501px;
  border-radius: 0 33px 33px;
  padding: 8px;
}

.card__icon {
  margin-bottom: 28px;
}

.card__text {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.19;
}

.card__box {
  gap: 16px;
  display: flex;
}

.card__thumb {
  border-right: 1px solid #7eb101;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 16px;
}

.card__subtitle {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: var(--backdrop-bg-cl);
  transition: opacity var(--animation-slow), visibility var(--animation-slow);
  padding-top: 60px;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop--is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  max-width: 458px;
  background-color: var(--main-yellow);
  transition: transform var(--animation);
  border-radius: 30px;
  padding: 32px;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

@media screen and (max-width: 767px) {
  .modal {
    margin: 0 auto;
    padding: 20px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }
}

.backdrop--is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.1);
}

.modal__close-btn {
  width: 32px;
  height: 32px;
  color: var(--logo-dark-cl);
  background-color: var(--main-light);
  cursor: pointer;
  transition: color var(--animation);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal__close-btn:hover, .modal__close-btn:focus {
  color: var(--accent-cl);
}

.order-form__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.21;
  display: block;
}

.order-form__group {
  margin-bottom: 32px;
}

.order-form__wrap {
  margin-bottom: 24px;
  display: block;
  position: relative;
}

.order-form__wrap:last-child {
  margin: 0;
}

.order-form__label {
  margin-bottom: 2px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.38;
  display: block;
}

.order-form__input {
  width: 100%;
  height: 48px;
  background-color: var(--main-light);
  transition: border-color var(--animation);
  border: 1px solid rgba(18, 18, 18, .12);
  border-radius: 30px;
  outline: rgba(0, 0, 0, 0);
  padding: 14px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  display: block;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, .15);
}

.order-form__input:hover, .order-form__input:focus, .order-form__input:required:valid:not(:placeholder-shown) {
  border-color: var(--main-green);
}

.order-form__input:required:invalid:not(:placeholder-shown) {
  border-color: var(--form-red);
}

.order-form__notification {
  width: 100%;
  opacity: 0;
  transition: opacity var(--animation);
  position: absolute;
  bottom: -20px;
  left: 12px;
}

.order-form__input:required:invalid:not(:placeholder-shown) ~ .order-form__notification {
  opacity: 1;
}

.order-form__desc {
  color: var(--form-red);
  font-size: 11px;
  font-weight: 500;
  line-height: 1.36;
}

.order-form__btn {
  background-color: var(--main-light);
  border: 2px solid var(--main-green);
  transition: background-color var(--animation);
  margin: 0 auto;
  padding: 8px 36px;
